/**
 * WordPress dependencies
 */

import WavePlayerServerSideRender from './waveplayer-ssr';

import { getBlobByURL, isBlobURL } from '@wordpress/blob';
import {
	Disabled,
	Button,
	PanelBody,
	SelectControl,
	RangeControl,
	ToggleControl,
	Toolbar,
	ColorPicker,
	withNotices,
} from '@wordpress/components';
import {
	RichText,
	mediaUpload,
} from '@wordpress/editor';
import {
	BlockControls,
	InspectorControls,
	BlockIcon,
	MediaPlaceholder,
	PanelColorSettings,
} from '@wordpress/block-editor';

import { Component, Fragment, RawHTML, createRef } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import icon from './icon';

const ALLOWED_MEDIA_TYPES = [ 'audio' ];

class WavePlayer extends Component {
	constructor() {
		super( ...arguments );
		this.state = {
			editing: ( ! this.props.attributes.url && ! this.props.attributes.ids ),
		};

		this.toggleAttribute = this.toggleAttribute.bind( this );
		this.onSelectURL = this.onSelectURL.bind( this );
		this.onUploadError = this.onUploadError.bind( this );
	}

	componentDidMount() {
		const { attributes, mediaUpload, noticeOperations, setAttributes } = this.props;
		const { ids, url = '' } = attributes;

		if ( ! ids && isBlobURL( url ) ) {
			const file = getBlobByURL( url );

			if ( file ) {
				mediaUpload( {
					filesList: [ file ],
					onFileChange: ( [ { ids: mediaId, url } ] ) => {
						setAttributes( { ids: mediaId, url: url } );
					},
					onError: ( e ) => {
						setAttributes( { url: undefined, ids: undefined } );
						this.setState( { editing: true } );
						noticeOperations.createErrorNotice( e );
					},
					allowedTypes: ALLOWED_MEDIA_TYPES,
				} );
			}
		}
	}

	toggleAttribute( attribute ) {
		return ( newValue ) => {
			if ( newValue === window.wvplVars.options[ attribute ] ) newValue = undefined;
			this.props.setAttributes( { [ attribute ]: newValue } );
		};
	}

	updateInstance() {
		return () => {
			window.WavePlayer.stop()
			window.WavePlayer.loadInstances('[data-block="'+this.props.clientId+'"]');
		}
	}

	onSelectURL( newURL ) {
		const { attributes, setAttributes } = this.props;
		let { url } = attributes;

		if ( newURL !== url ) {
			setAttributes( { url: newURL, ids: undefined } );
			url = newURL;
		}

		this.setState( { editing: false } );
	}

	onUploadError( message ) {
		const { noticeOperations } = this.props;
		noticeOperations.removeAllNotices();
		noticeOperations.createErrorNotice( message );
	}

	getAutoplayHelp( checked ) {
		return checked ? __( 'Note: Autoplaying audio may cause usability issues for some visitors.' ) : null;
	}

	render() {
		const { setAttributes, isSelected, className, noticeOperations, noticeUI, attributes } = this.props;
		const { caption, autoplay, repeat, shuffle, preload, url, ids, skin, palette, size, style, shape, wave_mode, gap_width, cursor_width, override_wave_colors, wave_color, wave_color_2, progress_color, progress_color_2, cursor_color, cursor_color_2, wave_animation, wave_normalization, amp_freq_ratio } = attributes;
		const { editing } = this.state;
		const switchToEditing = () => {
			this.setState( { editing: true } );
		};
		const skinSupport = ( property ) => {
			const support = window.wvplVars.skins.find( s => s.skin === skin ).support
			return ( support && support.includes( property ) )
		}
		const onSelectAudio = ( media ) => {
			if ( ! media ) {
				setAttributes( { url: undefined, ids: undefined } );
				switchToEditing();
				return;
			}
			const newIDs = media.map( ({id}) => id ).filter( (id) => id ).join(',');
			setAttributes( { url: undefined, ids: newIDs } );
			this.setState( { ids: newIDs, editing: false } );
		};
		if ( editing ) {
			let mediaIDs = ids || '';
			mediaIDs = mediaIDs.split(',').filter( (id) => id ).map( (id) => ({id: Number(id)}) );
			const value = ids ? mediaIDs : { src: url };
			return (
				<MediaPlaceholder
					labels={ {
						title: 'WavePlayer',
						instructions: __( 'Drag or upload your audio files, or select them from your Media library.' )
					} }
					icon={ <BlockIcon icon={ icon } /> }
					className={ className }
					onSelect={ onSelectAudio }
					onSelectURL={ this.onSelectURL }
					accept="audio/*"
					allowedTypes={ ALLOWED_MEDIA_TYPES }
					multiple={ 'add' }
					value={ value }
					notices={ noticeUI }
					onError={ this.onUploadError }
				/>
			);
		}

		/* eslint-disable jsx-a11y/no-static-element-interactions, jsx-a11y/onclick-has-role, jsx-a11y/click-events-have-key-events */
		return (
			<>
				<BlockControls>
					<Toolbar>
						<Button
							className="components-icon-button components-toolbar__control"
							label={ __( 'Edit WavePlayer' ) }
							onClick={ switchToEditing }
							icon="edit"
						/>
					</Toolbar>
				</BlockControls>
				<InspectorControls>
					<PanelBody title={ __( 'Player Settings' ) } initialOpen={ false }>
						<ToggleControl
							label={ __( 'Autoplay' ) }
							onChange={ this.toggleAttribute( 'autoplay' ) }
							checked={ autoplay }
						/>
						<ToggleControl
							label={ __( 'Repeat' ) }
							onChange={ this.toggleAttribute( 'repeat' ) }
							checked={ repeat }
						/>
						<ToggleControl
							label={ __( 'Shuffle' ) }
							onChange={ this.toggleAttribute( 'shuffle' ) }
							checked={ shuffle }
						/>
						<SelectControl
							label={ __( 'Preload' ) }
							value={ undefined !== preload ? preload : 'none' }
							// `undefined` is required for the preload attribute to be unset.
							onChange={ ( value ) => setAttributes( { 'preload': ( 'none' !== value ) ? value : undefined } ) }
							options={ [
								{ value: 'auto', label: __( 'Auto' ) },
								{ value: 'metadata', label: __( 'Metadata' ) },
								{ value: 'none', label: __( 'None' ) },
							] }
						/>
					</PanelBody>
					<PanelBody title={ __( 'Visual Aspect' ) } initialOpen={ false }>
						<SelectControl
							label={ __( 'Skin' ) }
							value={ undefined !== skin ? skin : 'w3-standard' }
							onChange={ ( value ) => setAttributes( { skin: value } ) }
							options={ window.wvplVars.skins.map( (s) => { return {value: s.skin, label: s.name } } ) }
						/>
						<SelectControl
							label={ __( 'Palette' ) }
							value={ undefined !== palette ? palette : '' }
							onChange={ ( value ) => setAttributes( { palette: value } ) }
							options={ window.wvplVars.palettes.map( (p) => { return {value: p.colors, label: p.name } } ) }
						/>
						<ToggleControl
							label={ __( 'Palette overrides default waveform colors', 'waveplayer' ) }
							onChange={ this.toggleAttribute( 'override_wave_colors' ) }
							checked={ override_wave_colors }
						/>
						<SelectControl
							label={ __( 'Style' ) }
							value={ undefined !== style ? style : 'light' }
							onChange={ ( value ) => setAttributes( { style: value } ) }
							options={ [
								{ value: 'light', label: __( 'Light' ) },
								{ value: 'dark', label: __( 'Dark' ) },
								{ value: 'color-scheme', label: __( 'Follow device color scheme' ) },
							] }
						/>
						{ skinSupport( 'size' ) && (
							<SelectControl
								label={ __( 'Size' ) }
								value={ undefined !== size ? size : 'lg' }
								onChange={ ( value ) => setAttributes( { size: value } ) }
								options={ [
									{ value: 'lg', label: __( 'Large' ) },
									{ value: 'md', label: __( 'Medium' ) },
									{ value: 'sm', label: __( 'Small' ) },
									{ value: 'xs', label: __( 'Extra Small' ) },
								] }
							/>
						)}
						{ skinSupport( 'shape' ) && (
							<SelectControl
								label={ __( 'Shape' ) }
								value={ undefined !== shape ? shape : 'square' }
								onChange={ ( value ) => setAttributes( { shape: value } ) }
								options={ [
									{ value: 'square', label: __( 'Square' ) },
									{ value: 'circle', label: __( 'Circle' ) },
									{ value: 'rounded', label: __( 'Rounded' ) },
								] }
							/>
						)}
					</PanelBody>
					<PanelBody  title={ __( 'Waveform Settings' ) } initialOpen={ false }>
						<RangeControl
					        label="Bar width"
					        value={ Number(wave_mode) }
					        onChange={ ( value ) => setAttributes( { wave_mode: value } ) }
					        min={ 1 }
					        max={ 10 }
					    />
						<RangeControl
					        label="Gap width"
					        value={ Number(gap_width) }
					        onChange={ ( value ) => setAttributes( { gap_width: value } ) }
					        min={ 0 }
					        max={ 10 }
					    />
						<SelectControl
							label={ __( 'Cursor Width' ) }
							value={ cursor_width }
							onChange={ ( value ) => setAttributes( { cursor_width: value } ) }
							options={ [
								{ value: '0', label: __( 'No cursor' ) },
								{ value: '1', label: __( 'Thin' ) },
								{ value: '2', label: __( 'Normal' ) },
								{ value: '4', label: __( 'Thick' ) },
							] }
						/>
						<ToggleControl
							label={ __( 'Visual Normalization', 'waveplayer' ) }
							onChange={ this.toggleAttribute( 'wave_normalization' ) }
							checked={ wave_normalization }
						/>
						<SelectControl
							label={ __( 'Visual Compression', 'waveplayer' ) }
							value={ Number(wave_animation) }
							onChange={ ( value ) => setAttributes( { wave_animation: value } ) }
							options={ [
								{ value: '1', label: __( 'None (linear)', 'waveplayer' ) },
								{ value: '2', label: __( 'Moderate (square)', 'waveplayer' ) },
								{ value: '3', label: __( 'High (cubic)', 'waveplayer' ) },
								{ value: '4', label: __( 'Very high (4th Order)', 'waveplayer' ) },
								{ value: '5', label: __( 'Extreme (5th order)', 'waveplayer' ) }
							] }
						/>
						<SelectControl
							label={ __( 'Visual Asymmetry', 'waveplayer' ) }
							value={ Number(wave_animation) }
							onChange={ ( value ) => setAttributes( { wave_animation: value } ) }
							options={ [
								{ value: '1', label: __( '1/2 + 1/2', 'waveplayer' ) },
								{ value: '2', label: __( '2/3 + 1/3', 'waveplayer' ) },
								{ value: '3', label: __( '3/4 + 1/4', 'waveplayer' ) },
								{ value: '4', label: __( '4/5 + 1/5', 'waveplayer' ) },
								{ value: '99999', label: __( 'Top Only', 'waveplayer' ) },
								{ value: '0.00001', label: __( 'Bottom Only', 'waveplayer' ) },
							] }
						/>
						<SelectControl
							label={ __( 'Wave Animation', 'waveplayer' ) }
							value={ Number(wave_animation) }
							onChange={ ( value ) => setAttributes( { wave_animation: value } ) }
							options={ [
								{ value: '1', label: __( 'No animation', 'waveplayer' ) },
								{ value: '0.85', label: __( 'Slow', 'waveplayer' ) },
								{ value: '0.7', label: __( 'Smooth', 'waveplayer' ) },
								{ value: '0.55', label: __( 'Normal', 'waveplayer' ) },
								{ value: '0.4', label: __( 'Fast', 'waveplayer' ) },
								{ value: '0.25', label: __( 'Hectic', 'waveplayer' ) },
							] }
						/>
						{ Number(wave_animation) < 1 && (
							<SelectControl
								label={ __( 'Amp/Freq Balance', 'waveplayer' ) }
								value={ Number(amp_freq_ratio) }
								onChange={ ( value ) => setAttributes( { amp_freq_ratio: value } ) }
								options={ [
									{ value: '4', label: __( 'Mostly amplitude', 'waveplayer' ) },
									{ value: '2', label: __( 'More amplitude', 'waveplayer' ) },
									{ value: '1', label: __( 'Equally', 'waveplayer' ) },
									{ value: '0.5', label: __( 'More Frequency', 'waveplayer' ) },
									{ value: '0.25', label: __( 'Mostly Frequency', 'waveplayer' ) },
									{ value: '0.000061', label: __( 'Frequency only', 'waveplayer' ) },
								] }
							/>
						)}
					</PanelBody>
					<PanelColorSettings
						title={ __( 'Waveform Colors', 'waveplayer' ) }
						initialOpen={ false }
						colorSettings={ [
	                        {
	                            value: wave_color,
	                            onChange: ( colorValue ) => setAttributes( { 'wave_color': colorValue } ),
	                            label: __( 'Waveform Color 1', 'waveplayer' ),
	                        },
	                        {
	                            value: wave_color_2,
	                            onChange: ( colorValue ) => setAttributes( { 'wave_color_2': colorValue } ),
	                            label: __( 'Waveform Color 2', 'waveplayer' ),
	                        },
						] }
					>
					</PanelColorSettings>
					<PanelColorSettings
						title={ __( 'Progress Colors', 'waveplayer' ) }
						initialOpen={ false }
						colorSettings={ [
	                        {
	                            value: progress_color,
	                            onChange: ( colorValue ) => setAttributes( { 'progress_color': colorValue } ),
	                            label: __( 'Progress Color 1', 'waveplayer' ),
	                        },
	                        {
	                            value: progress_color_2,
	                            onChange: ( colorValue ) => setAttributes( { 'progress_color_2': colorValue } ),
	                            label: __( 'Progress Color 2', 'waveplayer' ),
	                        },
						] }
					>
					</PanelColorSettings>
					<PanelColorSettings
						title={ __( 'Cursor Colors', 'waveplayer' ) }
						initialOpen={ false }
						colorSettings={ [
	                        {
	                            value: cursor_color,
	                            onChange: ( colorValue ) => setAttributes( { 'cursor_color': colorValue } ),
	                            label: __( 'Cursor Color 1', 'waveplayer' ),
	                        },
	                        {
	                            value: cursor_color_2,
	                            onChange: ( colorValue ) => setAttributes( { 'cursor_color_2': colorValue } ),
	                            label: __( 'Cursor Color 2', 'waveplayer' ),
	                        },
	                    ] }
					>
					</PanelColorSettings>
				</InspectorControls>
				<Disabled>
					<WavePlayerServerSideRender
						className={ className }
						block="waveplayer/block"
						attributes={ this.props.attributes }
						onChange={ this.updateInstance() }
					/>
				</Disabled>
			</>
		);
		/* eslint-enable jsx-a11y/no-static-element-interactions, jsx-a11y/onclick-has-role, jsx-a11y/click-events-have-key-events */
	}
}

export default withNotices( WavePlayer );
