/**
 * WordPress dependencies
 */
import { createBlobURL } from '@wordpress/blob';
import { createBlock } from '@wordpress/blocks';

const transforms = {
	from: [
		{
			type: 'files',
			isMatch( files ) {
				for ( let file of files ) {
					if ( file.type.indexOf( 'audio/' ) !== 0 )
						return false;
				}
				return true;
			},
			transform( files ) {
				const file = files[ 0 ];
				// We don't need to upload the media directly here
				// It's already done as part of the `componentDidMount`
				// in the audio block
				const block = createBlock( 'waveplayer/block', {
					src: createBlobURL( file ),
				} );

				return block;
			},
		},
		{
			type: 'block',
			blocks: [ 'core/audio' ],
			transform: ( attributes ) => {
				return createBlock( 'waveplayer/block', {
					url: attributes.src,
				} );
			},
		},
		{
			type: 'shortcode',
			tag: 'playlist',
			attributes: {
				'ids': {
					type: 'string',
					shortcode: ( { named: { ids } } ) => {
						return ids;
					},
				}
			}
		},
		{
			type: 'shortcode',
			tag: 'waveplayer',
			attributes: {
				'ids': {
					type: 'string',
					shortcode: ( { named: { ids } } ) => {
						return ids;
					},
				},
				'url': {
					type: 'string',
					shortcode: ( { named: { url } } ) => {
						return url;
					},
				},
				'size': {
					type: 'string',
					shortcode: ( { named: { size } } ) => {
						return size;
					},
				},
				'shape': {
					type: 'string',
					shortcode: ( { named: { shape } } ) => {
						return shape;
					},
				},
				'style': {
					type: 'string',
					shortcode: ( { named: { style } } ) => {
						return style;
					},
				},
				'repeat': {
					type: 'string',
					shortcode: ( { named: { repeat } } ) => {
						return repeat;
					},
				},
				'autoplay': {
					type: 'string',
					shortcode: ( { named: { autoplay } } ) => {
						return autoplay;
					},
				},
				'music_genres': {
					type: 'string',
					shortcode: ( { named: { music_genres } } ) => {
						return music_genres;
					},
				},
				'wave_mode': {
					type: 'string',
					shortcode: ( { named: { wave_mode } } ) => {
						return wave_mode;
					},
				},
				'gap_width': {
					type: 'string',
					shortcode: ( { named: { gap_width } } ) => {
						return gap_width;
					},
				},
				'wave_color': {
					type: 'string',
					shortcode: ( { named: { wave_color } } ) => {
						return wave_color;
					},
				},
				'wave_color_2': {
					type: 'string',
					shortcode: ( { named: { wave_color_2 } } ) => {
						return wave_color_2;
					},
				},
				'progress_color': {
					type: 'string',
					shortcode: ( { named: { progress_color } } ) => {
						return progress_color;
					},
				},
				'progress_color_2': {
					type: 'string',
					shortcode: ( { named: { progress_color_2 } } ) => {
						return progress_color_2;
					},
				},
				'cursor_color': {
					type: 'string',
					shortcode: ( { named: { cursor_color } } ) => {
						return cursor_color;
					},
				},
				'cursor_color_2': {
					type: 'string',
					shortcode: ( { named: { cursor_color_2 } } ) => {
						return cursor_color_2;
					},
				},
				'cursor_width': {
					type: 'string',
					shortcode: ( { named: { cursor_width } } ) => {
						return cursor_width;
					},
				},

			},
		},
	],
};

export default transforms;
